import CustomController from "components/UI/common/Controller";
import NormalMultiSelect from "components/UI/common/NormalMultiSelect";
import React from "react";
import styles from "./index.module.scss";
import { IconButton } from "@mui/material";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import { useFieldArray } from "react-hook-form";
import { ReactComponent as RedDeleteIcon } from "assets/icons/redDeleteIcon.svg";

const CategoryTable = ({
  field,
  fieldName,
  register,
  key,
  watch,
  control,
  index,
  setValue,
  isLast,
  datalength,
  roomCategoryDropdownData = [],
  stayTypeDropdownData = [],
  handleCategoryAdd = () => { },
  deleteCategory = () => { },
}) => {

  return (
    <tr key={key}>
      <td
        style={{ width: "280px", verticalAlign: "baseline" }}
        className={isLast ? "border-bottom-0" : ""}
      >
        <div
          className={` d-flex align-items-center  ${styles.selectcontroller}`}
        >
          {console.log(`${fieldName}.category`)}
          <CustomController
            name={`${fieldName}.category`}
            control={control}
            defaultValue={`${fieldName}.category`}
            rules={{
              required: true,
            }}
            messages={{ required: "Room Category is required" }}
            render={({ field: { onChange, value, ref }, ...fields }) => {
              return (
                <NormalMultiSelect
                  {...fields}
                  selectRef={ref}
                  customStyle1
                  options={roomCategoryDropdownData}
                  name={`${fieldName}.category`}
                  isSearchable
                  placeholder="Select"
                  handleChange={({ target: { value } }) => {
                    onChange(value);
                  }}
                  menuPosition="fixed"
                />
              );
            }}
          />
          <div className={styles.line}> </div>
          {console.log(index)} {console.log(datalength)}{" "}
          <div>
            {index !== 1 ? (
              datalength.length === 1 ? (
                <IconButton className={`${styles.iconbtn} mx-1`}>
                  <AddCircleOutlineIcon
                    onClick={() => {
                      handleCategoryAdd({
                        category: "",
                        stayType: [],
                      });
                    }}
                    className={styles.circleAdd}
                  />
                </IconButton>
              ) : (
                <div style={{ width: "44px" }}></div>
              )
            ) : (
              <IconButton className={`${styles.iconbtn} mx-1`}>
                <RedDeleteIcon
                  onClick={() => deleteCategory(index)}
                  className={styles.circleAdd}

                />
              </IconButton>
            )}
          </div>
        </div>
      </td>
      <td
        style={{ width: "280px", verticalAlign: "baseline" }}
        className={isLast ? "border-bottom-0" : ""}
      >
        <CustomController
          name={`${fieldName}.stayType`}
          control={control}
          defaultValue={[]}
          rules={{
            required: true,
          }}
          messages={{ required: "Stay Type is required" }}
          render={({ field: { onChange, value, ref }, ...fields }) => {
            const twoDataOnly = value.map((data, index, array) => ({
              value: data.value,
              label:
                index === array.length - 1
                  ? `${`${data.label}`}`
                  : `${`${data.label}`},`,
            }));
            return (
              <NormalMultiSelect
                selectRef={ref}
                {...fields}
                isClearable={false}
                // className={`${styles.StayType} ` }
                options={stayTypeDropdownData}
                isMulti={true}
                showCheckbox={true}
                closeMenuOnSelect={false}
                name={`${fieldName}.stayType`}
                customStyle1
                isSearchable={false}
                placeholder="Select"
                showfooterButton
                handleChange={({ target: { value } }) => {
                  onChange(value);
                }}
                value={twoDataOnly}
                menuPosition="fixed"
                updatedValue={value}
              />
            );
          }}
        />

        <div></div>
      </td>
    </tr>
  );
};
export default CategoryTable;
